// Desktop & Tablet View

.navbar{
    padding: 1em 2em;
    display: flex;
    justify-content: space-between;
    align-items:center;
    background-color: $color2;
    border-bottom: .5px solid $color3;
    @include responsive(s){
        display: none;
    }

    &_toggle{
      display: flex;
      &_input {
        cursor: pointer;
        -webkit-appearance: none;
        position: relative;
        width: 57px;
        height: 27px;
        border: 2px solid #ccdbdc;
        outline: none;
        border-radius: 30px;
        transition: 300ms;
        &::before{
          content: "";
          position: absolute;
          top: 1.5px;
          left: 2px;
          width: 20px;
          height: 20px;
          background: #d1c9c9;
          border-radius: 50%;
          transition: background 300ms ease, transform 300ms ease;
        }
        &:checked{
          border-color: #7494ea;
          &::before{
            transform: translateX(30px);
            background: #7494ea;
          }
        }
      }
    }

    &_menu{
        display: flex;
        list-style: none;
        justify-content: space-around;
        width:400px;
        align-items:center;
        font-weight: 200;
        font-size: 18px;
        &_list{
          text-decoration: none;
          color: $color3;
          transition: color .2s ease-in;
          &:hover{
            color: $color1;
          }
      }
    }
}

.active{
  color: $color1;
}

// Mobile View

.mnavbar{
  display: none;
  @include responsive(s){
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 99;
}
  &_menu{
    background-color: $color4;
    display: flex;
    padding: .5em 0;
    position: fixed;
    bottom: 0px;
    width: 100%;
    justify-content: space-around;
    align-items: center;
    z-index: 1;
    &_list{
      display: flex;
      align-items: center;
      position: relative;
      text-decoration: none;
      flex-direction: column;
      justify-content: center;
      path{
        fill: transparent;
        stroke-width: 20;
        stroke: $color3;
      }
      span{
        color: $color3;
        display: block;
        margin-top: 2px;
        font-weight: 300;
        font-size: 10px;
      }
      img{
        width: 25px;
        border-radius: 50%;
      }
    }
  }
}

.mactive path{
  fill: transparent;
  stroke-width: 30;
  stroke: $color1;
}

.mactive span{
  transition: color 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
  color: $color1;
  display: block;
}

.mactive path{
  animation-name: offset;
  animation-iteration-count: 1;
  animation-duration: 330ms;
  transition: 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
}

@keyframes offset {
  0%{
    transform: scale(1);
    stroke: $color2;  
  }
  50% {
  transform: scale(.8);
  }
  100% {
    transform: scale(1);
    
  stroke: $color1;
  }
}
